 put this snippet in the appropriate selector:\r\n     @include bp(tiny) {\r\n         background-color: purple;\r\n     }\r\n     Replace \"tiny\" with \"medium\" or \"big\" as necessary.\r\n*************************************/\r\n\r\n@mixin bp($point) {\r\n\r\n  $bp-xsmall: \"(min-width: 320px)\";\r\n  $bp-teeny: \"(min-width: 480px)\";\r\n  $bp-tiny: \"(min-width: 600px)\";\r\n  $bp-small: \"(min-width: 650px)\";\r\n  $bp-medium: \"(min-width: 800px)\";\r\n  $bp-big: \"(min-width: 1000px)\";\r\n\r\n  @if $point == big {\r\n    @media #{$bp-big} { @content; }\r\n  }\r\n  @else if $point == medium {\r\n    @media #{$bp-medium} { @content; }\r\n  }\r\n  @else if $point == small {\r\n    @media #{$bp-small}  { @content; }\r\n  }\r\n  @else if $point == tiny {\r\n    @media #{$bp-tiny}  { @content; }\r\n  }\r\n  @else if $point == teeny {\r\n    @media #{$bp-teeny}  { @content; }\r\n  }\r\n  @else if $point == xsmall {\r\n    @media #{$bp-xsmall}  { @content; }\r\n  }\r\n}\r\n\r\n// REM Font Adjustments\r\n@mixin font-size($sizeValue) {\r\n  font-size: ($sizeValue) + px;\r\n  font-size: ($sizeValue / 10) + rem;\r\n}\r\n\r\n@mixin letter-spacing($spacingValue) {\r\n  letter-spacing: ($spacingValue) + px;\r\n  letter-spacing: ($spacingValue / 10) + rem;\r\n}\r\n\r\n@mixin line-height($heightValue) {\r\n  line-height: ($heightValue) + px;\r\n  line-height: ($heightValue / 10) + rem;\r\n}\r\n\r\n// PLACEHOLDER\r\n// NOTE: Vendor-prefixed selectors must be on separate blocks, because one invalid/unknown\r\n//       selector will invalidate the whole block.\r\n@mixin placeholder {\r\n  &:-ms-input-placeholder      { @content; }  // IE\r\n  &::-ms-input-placeholder     { @content; }  // Edge\r\n  &::-webkit-input-placeholder { @content; }  // QQ Browser\r\n  &::placeholder               { @content; }  // Chrome/Firefox/Safari\r\n}\r\n\r\n@mixin rotate($degrees) {\r\n  -moz-transform: rotate($degrees);\r\n  -webkit-transform: rotate($degrees);\r\n  -o-transform: rotate($degrees);\r\n  -ms-transform: rotate($degrees);\r\n  transform: rotate($degrees);\r\n  transition: transform 150ms;\r\n  transition-timing-function: ease-in-out;\r\n}\r\n\r\n@mixin codeblock($bgcolor) {\r\n  background-color: $bgcolor;\r\n  border-radius: 5px;\r\n  padding: 20px;\r\n  margin: 0 auto; // was 24\r\n  border: 0.5px solid $lightgray;\r\n}\r\n\r\n// INFO CARD SKELETON\r\n@mixin card($height, $width) {\r\n  height: $height;\r\n  width: $width;\r\n  background-color: $white;\r\n  border-radius: 4px;\r\n  box-shadow: 0 2px 2px rgba($black, 0.24), 0 0 2px rgba($black, 0.12);\r\n  box-sizing: border-box;\r\n  transition: box-shadow .5s;\r\n\r\n  &:hover {\r\n    box-shadow: 0 8px 8px rgba($black, 0.24), 0 0 8px rgba($black, 0.12);\r\n    text-decoration: none;\r\n  }\r\n}\r\n",".mat-badge-content{font-weight:600;font-size:12px;font-family:Roboto, \"Helvetica Neue\", sans-serif}.mat-badge-small .mat-badge-content{font-size:9px}.mat-badge-large .mat-badge-content{font-size:24px}.mat-h1,.mat-headline,.mat-typography h1{font:400 24px/32px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:normal;margin:0 0 16px}.mat-h2,.mat-title,.mat-typography h2{font:500 20px/32px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:normal;margin:0 0 16px}.mat-h3,.mat-subheading-2,.mat-typography h3{font:400 16px/28px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:normal;margin:0 0 16px}.mat-h4,.mat-subheading-1,.mat-typography h4{font:400 15px/24px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:normal;margin:0 0 16px}.mat-h5,.mat-typography h5{font:400 calc(14px * 0.83)/20px Roboto, \"Helvetica Neue\", sans-serif;margin:0 0 12px}.mat-h6,.mat-typography h6{font:400 calc(14px * 0.67)/20px Roboto, \"Helvetica Neue\", sans-serif;margin:0 0 12px}.mat-body-strong,.mat-body-2{font:500 14px/24px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:normal}.mat-body,.mat-body-1,.mat-typography{font:400 14px/20px Roboto, \"Helvetica Neue\", sans-serif;letter-spacing:norm